/* Layer  */

.profile_image {
    width: 400px;
    height: 400px;
    margin-top: 100px;
    /* filter: hue-rotate(160deg) saturate(150%); */
}




.profile_bg_layer_1 {
    width: 500px;
    height: 500px;
    overflow: hidden;
    background-color: var(--secondary-color-secondary-extra-dark);
}

.profile_bg_layer_2 {
    width: 450px;
    height: 450px;
    background-color: var(--secondary-color-secondary-dark);
}

.profile_bg_layer_3 {
    width: 350px;
    height: 350px;
    background-color: var(--secondary-color-secondary-light);
}

.profile_bg_layer_4 {
    width: 300px;
    height: 300px;
    background-color: var(--secondary-color-secondary-double-extra-dark);
}



.profile_bg_layer_1,
.profile_bg_layer_2,
.profile_bg_layer_3,
.profile_bg_layer_4 {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}

.profile_bg_layer_4 {
    -webkit-perspective: 1000px;
            perspective: 1000px;
}

.profile_image {
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.profile_image:hover {
    -webkit-transform: rotateX(10deg) rotateY(10deg);
            transform: rotateX(10deg) rotateY(10deg);
}


@media (max-width: 1200px) {
    .profile_bg_layer_1 {
        width: 400px;
        height: 400px;
        overflow: hidden;
    }

    .profile_bg_layer_2 {
        width: 350px;
        height: 350px;
    }

    .profile_bg_layer_3 {
        width: 250px;
