/* cursor */
@-webkit-keyframes blink {

    0%,
    100% {
        opacity: 0;
        /* Fade out at the start and end */
    }

    50% {
        opacity: 1;
        /* Fully visible in the middle */
    }
}
@keyframes blink {

    0%,
    100% {
        opacity: 0;
        /* Fade out at the start and end */
    }

    50% {
        opacity: 1;
        /* Fully visible in the middle */
    }
}

.continue_blink {
    -webkit-animation: blink 0.3s infinite;
            animation: blink 0.3s infinite;
    /* Adjust the duration as needed */
}

